/* wrapper */
#wrapper {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
}

.layout {
    margin: 0 auto;
    position: relative;
    width: 1400px;
}

footer {
    display: block;
    width: 100%;
    margin: 0 0 0;
    padding: 0 0 0;
}

main {
    display: block;
    width: 100%;
    position: relative;
}

header {
    width: 100%;
    display: block;
    position: relative;
}
